import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import * as yup from "yup";
import auth from "./components/routing/auth";
import { signInWithGoogle2 } from "./base.js"
import Swal from "sweetalert2";
import { DotWave } from '@uiball/loaders'
import {RealTypo} from "./components/RealTypo"



const schema = yup.object().shape({
  personalID: yup
    .number()
    .typeError('המספר האישי חייב להיות מספר')
    .test('len', 'המספר האישי חייב להכיל 7 ספרות', val => val.toString().length === 7),
});

export const Login = () => {

  const [isLoading, setLoading] = useState(false);
  const { setValues, data,overRideValues } = useData();
  const history = useNavigate();
  console.log("data is now="+data)
  const { register, handleSubmit, errors } = useForm({
    defaultValues: {
      personalID: ""
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });

  let adminCheck = async (data2, docs) => {
    let info = Object.values(data2).toString();
    docs.forEach((doc1) => {
      data2 = doc1.data();
    });
    console.log(data2)
    return [data2,info];
  };
  
  const onSubmit = async (data) => {
    setLoading(true);
    let info = Object.values(data).toString();
    let adminDocs = await signInWithGoogle2(info);
    console.log(adminDocs)
    if (Array.isArray(adminDocs))
      {
        let data3 = await adminCheck(data, adminDocs[0]);
    // if (data !== data2) {
    //   setLoading(false);
    //   auth.logout(() => { history("./"); });
    //   Swal.fire({
    //     icon: "error",
    //     title: "החייל כבר מילא בקשה בעבר!",
    //     text: "במקרה ומדובר בשגיאה, אנא תקן זאת בחלון המפקדים.",
    //     confirmButtonText: "אוקיי"
    //   });
    // }
    // else {
    if (adminDocs[1] === "admin") {
      setLoading(false);
      auth.logout(() => { history("./"); });
      Swal.fire({
        icon: "success",
        title: data3[0]["mpFirstName"] + " " + data3[0]["mpLastName"] + ", ברוך הבא!",
        text: "לחץ אוקיי כדי לעבור לחלון לאישור חיילים",
        confirmButtonText: "אוקיי"
      });
      auth.login(() => { history("./PickAsolider"); })
      data3[0]["mpID"] = data3[1];
      overRideValues();
      setValues(data3[0]);
    }
    else {
      if (adminDocs[1] === "member") {
        auth.login(() => { history("./step1"); })
        data3[0]["personalID"] = data3[1];
        overRideValues();
        setValues(data3[0]);
      } 
      }
    }
    else{
      if (adminDocs === "mailError") {
        setLoading(false);
        auth.logout(() => { history("./"); });
        Swal.fire({
          icon: "error",
          title: "המייל נמצא כבר בשימוש עם מספר אישי אחר 😔",
          text: "אנא נסה שנית",
          confirmButtonText: "אוקיי"
        });
      }
      if (adminDocs === "personalIDerror") {
        setLoading(false);
        auth.logout(() => { history("./"); });
        Swal.fire({
          icon: "error",
          title: "המספר האישי נמצא כבר בשימוש עם מייל אחר 😔",
          text: "אנא נסה שנית",
          confirmButtonText: "אוקיי"
        });
      }
      if (adminDocs === "generalError") {
        setLoading(false);
        auth.logout(() => { history("./"); });
        Swal.fire({
          icon: "error",
          title: "לא הצלחנו לחבר אותך 😔",
          text: "אנא בדוק שהמייל והמספר אישי איתם ניסית להתחבר שייכים לך",
          confirmButtonText: "אוקיי"
        });
      }
      if (adminDocs === "closedByUser") {
          setLoading(false);
          auth.logout(() => { history("./"); });
      };
  // };
    }
  };

  return (
    <MainContainer >
      <RealTypo component="h2" variant="h5">הזדהות</RealTypo>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Input2
          dir="rtl"
          ref={register}
          id="personalID"
          type="number"
          label="מספר אישי"
          name="personalID"
          error={!!errors.personalID}
          helperText={errors?.personalID?.message}
        />
        <PrimaryButton  disabled={isLoading}>
          {isLoading && (
            <DotWave
              size={40}
              speed={2.5}
              color="gray"
            />
          )}
          {!isLoading && <span>כניסה</span>}
        </PrimaryButton >
      </Form>
    </MainContainer>
  );
};
