import React, { useState } from "react";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import FormHelperText from '@mui/material/FormHelperText';
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Checkbox from '@mui/material/Checkbox';
import FormGroup from '@mui/material/FormGroup';
import Grid from "@material-ui/core/Grid";
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import MenuItem from '@mui/material/MenuItem';
import * as yup from "yup";
import ReactHookFormSelect from "./components/ranks"
import {RealTypo} from "./components/RealTypo"


const schema = yup.object().shape({
  firstName: yup
    .string()
    .matches(/^([^0-9]*)$/, "השם הפרטי לא יכול לכלול מספרים")
    .required("חובה למלא את השם הפרטי"),
  lastName: yup
    .string()
    .matches(/^([^0-9]*)$/, "השם משפחה לא יכול לכלול מספרים")
    .required("חובה למלא את השם משפחה"),
  isDati: yup
    .boolean(),
    datiORishylabel: yup
    .string()
    .required("חובה לבחור את סוג הבקשה")
  ,
  shapeOfBeard: yup
    .string()
    .required("חובה לבחור את סוג הזקן")
  ,
  ranks: yup
    .string()
    .required("חובה לבחור את הדרגה")
  ,
  date: yup
    .string()

});



export const Step1 = () => {
  const [value, setValue] = useState(new Date());

  const { setValues, data } = useData();
  const history = useNavigate();
  const [RegOffice, setRegOffice] = useState(data.datiORishylabel);
  const handleChangeRegOffice = (event) => {
    if (event.target.value === 'ishy')
      {data["isDati"] = false;
      setChecked(false);
      }
    setRegOffice(event.target.value);
  };

  const [RegOffice2, setRegOffice2] = useState(data.shapeOfBeard);
  const handleChangeRegOffice2 = (event) => {
    setRegOffice2(event.target.value);
  };
  if (data["isDati"] === undefined)
  {
    data["isDati"] = false;
  }
  console.log(data["isDati"])
  if (data["isDati"] === undefined) {
    console.log("isdati is undefined")
    data["isDati"] = false;
  }
  const [checked, setChecked] = React.useState(JSON.parse(data["isDati"]));

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };
  const { register, handleSubmit, errors, control } = useForm({
    defaultValues: {
      firstName: data.firstName,
      lastName: data.lastName,
      datiORishylabel: data.datiORishylabel,
      shapeOfBeard: data.shapeOfBeard,
      isDati: data.isDati,
      ranks: data.ranks,
      date: data.date
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });


  const onSubmit = async (data) => {
    const { value: accept } = await Swal.fire({
      title: 'הצהרת החייל:',
      width: 600,
      html: `
      <style>
      li {
  font-size: 90%;
  text-align: right;
}
sub {
  font-size: 50%;
  text-align: right;
}
right-align {
  text-align: right;
}
.swal2-header {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;

}
.swal2-content {
  font-family: 'Open Sans', sans-serif;
  text-align: right;
}
</style>
      <ul class="right-align" dir='rtl'>
      
 <p> <li>הזקן או השפם מהווה חלק מדמותי, מהווייתי או מזהותי.
  </li></p>
  <p><li> אני מתחייב בזאת כי אקפיד על זקן או שפם מסודר והולם את ההופעה הצבאית הנאותה.
  </li></p>
  <p> <li> ידוע לי, כי אם אגלח את זקני או שפמי, היתר חריג זה בטל (למעט אם תהא לכך סיבה רפואית, ויהיה בידי האישור הרפואי המצביע על כך).
  </li></p>
  <p> <li> על החייל לשאת את טופס האישור החריג בכל עת ולהציגו לשוטר צבאי, למפקד או לנגד היחידה, אם יידרש לכך.
  </li></p>
  <sub >מתוך: הק״א 33-05-01 - ״כללי ההופעה והלבוש״ עמוד 98 נספח יא׳</sub>
  </ul> `,
      input: 'checkbox',
      inputValue: 0,
      padding: '1em',
      inputPlaceholder :
        'אני מאשר את הצהרתי.',
      confirmButtonText:
        'המשך <i class="fa fa-arrow-right"></i>',
      inputValidator: (result) => {
        return !result && 'חובה לאשר את ההצהרה כדי להמשיך!'
      }
    })
    
    if (accept) {
      console.log(data);
      setValues(data);
      history("/Signature");
    }
  };


  console.log(data)

  return (
    <MainContainer >
      <RealTypo component="h2" variant="h5">פרטי הבקשה</RealTypo>
      <Stack
        spacing={40}
      >
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Box dir='rtl' m={2}>
            <Grid container
              direction="row"
              alignItems="center"
              spacing={2}>

              <Grid item container
                direction="row"
                alignItems="flex-end"
                justify="center" md={4}>
                <Input2
                  dir="rtl"
                  ref={register}
                  id="firstName"
                  type="text"
                  label="שם פרטי"
                  name="firstName"
                  error={!!errors.firstName}
                  style={{textAlign:"right", marginRight: '0px'}}
                  helperText={errors?.firstName?.message}
                />
              </Grid>

              <Grid item container
                direction="row"
                alignItems="flex-end"
                justify="center" md={4}>
                <Input2
                  dir="rtl"
                  ref={register}
                  id="lastName"
                  type="text"
                  label="שם משפחה"
                  name="lastName"
                  error={!!errors.lastName}
                  style={{textAlign:"right", marginRight: '0px'}}
                  helperText={errors?.lastName?.message}
                />

              </Grid>

              <Grid item container
                direction="row"
                alignItems="flex-end"
                justify="center" md={4}>


                  <ReactHookFormSelect
                      error={!!errors.ranks}
                      value={errors?.ranks?.message}
                      id="ranks"
                      name="ranks"
                      label="דרגה"
                      dir='rtl'
                      control={control}
                      defaultValue=""
                >
                      <MenuItem value="טוראי">טוראי</MenuItem>
                      <MenuItem value="רב״ט">רב״ט</MenuItem>
                      <MenuItem value="סמל">סמל</MenuItem>
                  <MenuItem value="סמ״ר">סמ״ר</MenuItem>
                 
                </ReactHookFormSelect>
               


              </Grid>

            </Grid>
          </Box>
          <Box dir='rtl' m={2}>
          <Grid item>
          <FormControl error={!!errors.datiORishylabel} dir='rtl'>
            <FormLabel id="datiORishylabel">סוג הבקשה</FormLabel>
            <RadioGroup>
              <FormControlLabel control={
                <Radio
                  name="datiORishylabel"
                  inputRef={register}
                  value="ishy"
                  checked={RegOffice === "ishy"}
                  onChange={handleChangeRegOffice}
                />} label="טעמים אישיים" />
              <FormControlLabel control={
                <Radio
                  name="datiORishylabel"
                  inputRef={register}
                  value="dati"
                  checked={RegOffice === "dati"}
                  onChange={handleChangeRegOffice}
                />} label="טעמים אישיים - דתיים" />
            </RadioGroup>
            <FormHelperText style={{textAlign:"right"}}>{errors?.datiORishylabel?.message}</FormHelperText>
          </FormControl>
          </Grid>
          <Box dir='rtl' m={2}>
          <Grid item>
          <FormControl error={!!errors.shapeOfBeard} dir='rtl'>
            <FormLabel id="shapeOfBeard">צורת הזקן או השפם:</FormLabel>
            <RadioGroup >
              <FormControlLabel control={
                <Radio
                  name="shapeOfBeard"
                  inputRef={register}
                  value="Full"
                  checked={RegOffice2 === "Full"}
                  onChange={handleChangeRegOffice2}
                />} label="זקן מלא ועבות מהפאות יורד עד הסנטר (כולל שפם)" />
              <FormControlLabel control={
                <Radio
                  name="shapeOfBeard"
                  inputRef={register}
                  value="French"
                  disabled = {RegOffice === 'dati'}
                  checked= {RegOffice2 === "French" && RegOffice !== 'dati'} 
                  onChange={handleChangeRegOffice2}
                />} label="זקן חלקי (צרפתי) - שמתחיל מאמצע הפנים, יורד עד הסנטר" />
              <FormControlLabel control={
                <Radio
                  name="shapeOfBeard"
                  inputRef={register}
                  value="Mos"
                  checked={RegOffice2 === "Mos"}
                  onChange={handleChangeRegOffice2}
                />}
                label="שפם" />
            </RadioGroup>
            <FormHelperText style={{textAlign:"right"}}>{errors?.shapeOfBeard?.message}</FormHelperText>
          </FormControl>
          </Grid>
          </Box>
            <Grid item>
              <FormLabel id="isDatilabel">האם אתה דתי?</FormLabel>
              <FormControlLabel control={<Checkbox
                disabled= {RegOffice !== 'dati'}
                checked={checked}
                onChange={handleChange}
                inputRef={register}
                name="isDati"
              />} label="אני מקיים אורח חיים דתי" />
            </Grid>
          </Box>

          <Grid item>
            <LocalizationProvider dateAdapter={AdapterDateFns}>

              <DatePicker
                dir="rtl"
                label="תאריך"
                id="date"
                inputFormat="dd/MM/yyyy"
                disabled
                value={value}
                inputRef={register}
                onChange={(newValue) => {
                  setValue(newValue);
                }}
                renderInput={(params) => <Input2 name="date"{...params} />}
              />
            </LocalizationProvider>
          </Grid>


            

          <PrimaryButton>הבא</PrimaryButton>
        </Form>
        </Stack>
    </MainContainer>
  );
};
